@import "utils";
@import "fonts";
@import "typography";
@import "header";
@import "reset";
@import "expertise";
@import "info";
@import "process";
@import "approach";
@import "testimonials";
@import "footer";
@import "vision";

body {
  background: #e5e5e5;
}

.main {
  box-sizing: border-box;
  width: 100%;
  overflow: hidden;
}

button {
  cursor: pointer;
  &:hover {
    box-shadow: 10px 10px 47px -10px rgba(97,56,56,0.75);
  }
}

a:hover {
  text-shadow: #fff 0 0 5px,
    #fff 0 0 10px,
    #fff 0 0 15px,
    #ff2d95 0 0 20px,
    #ff2d95 0 0 30px,
    #ff2d95 0 0 40px,
    #ff2d95 0 0 50px,
    #ff2d95 0 0 75px,
    -13px -15px 20px rgba(255,204,228,0.05);
}
